.sidebar {
    flex: 3;
    margin: 0px 20px;
    padding-bottom: 30px;
    background-color: #fdfbfb;
    border-radius: 10px;
    display: flex;
    flex-direction: column;
    align-items: center;
}

.sidebarItem {
    display: flex;
    flex-direction: column;
    align-items: center;
}

.sidebarTitle {
    margin: 10px;
    padding: 5px;
    width: 80%;
    border-top: 1px solid #a7a4a4;
    border-bottom: 1px solid #a7a4a4;
    font-family: 'Varela Round', sans-serif;   
    font-style: 12px;
    color: #222;
    font-weight: 600;
    line-height: 20px;
    text-align: center;
}

.sidebarImg {
    width: 300px;
    height: 400px;
    margin-top: 15px;
    object-fit: cover;
}

.sidebarItem > p {
    padding: 30px;
}

.sidebarList {
    list-style: none;
    margin-bottom: 30px;
}

.sidebarListItem {
    display: inline-block;
    width: 50%;
    margin-top: 15px;
    cursor: pointer;
}

.sidebarSocial {
    margin-top: 15px;
    width: 250px;
    display: flex;
    align-items: center;
    justify-content: center;
}

.sidebarIcon {
    font-size: 22px;
    margin: 10px;
    cursor: pointer;
}

@media (max-width: 570px){
    .sidebar {
        margin: 0px;
    }

    .sidebarTitle{
        margin: 0px;
        width: 90%;
    }

    .sidebarImg{
        width: 350px;
        height: 465px;
    }
}